content; // Chrome\n    width: -moz-fit-content; // Firefox\n    width: -webkit-fit-content; // Safari\n}\n\n.not-selectable() {\n    -webkit-touch-callout: none;\n    -webkit-user-select: none;\n    -khtml-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n    cursor: default;\n}\n\n.dash-spreadsheet {\n    &.dash-freeze-left,\n    &.dash-freeze-top {\n        .fit-content-polyfill();\n    }\n\n    &.dash-freeze-left {\n        max-width: 500px;\n    }\n\n    &.dash-freeze-top,\n    &.dash-virtualized {\n        max-height: 500px;\n    }\n}\n\n.dash-tooltip {\n    border: 1px solid #e4e4e4;\n    border-radius: 5px;\n    position: absolute;\n    z-index: 500;\n\n    .dash-table-tooltip {\n        position: relative;\n        background-color: #f6f6f6;\n        max-width: 300px;\n        min-width: 300px;\n        padding: 2px 10px;\n    }\n\n    &[data-attr-anchor='top'] {\n        margin-top: 10px;\n\n        &:after, &:before {\n            bottom: 100%;\n            left: 50%;\n            border: solid transparent;\n            content: \" \";\n            height: 0;\n            width: 0;\n            position: absolute;\n            pointer-events: none;\n        }\n\n        &:after {\n            border-color: transparent;\n            border-bottom-color: #f6f6f6;\n            border-width: 8px;\n            margin-left: -8px;\n        }\n\n        &:before {\n            border-color: transparent;\n            border-bottom-color: #e4e4e4;\n            border-width: 9px;\n            margin-left: -9px;\n        }\n    }\n\n    &[data-attr-anchor='bottom'] {\n        margin-bottom: 10px;\n\n        &:after, &:before {\n            top: 100%;\n            left: 50%;\n            border: solid transparent;\n            content: \" \";\n            height: 0;\n            width: 0;\n            position: absolute;\n            pointer-events: none;\n        }\n\n        &:after {\n            border-color: transparent;\n            border-top-color: #f6f6f6;\n            border-width: 8px;\n            margin-left: -8px;\n        }\n        &:before {\n            border-color: transparent;\n            border-top-color: #e4e4e4;\n            border-width: 9px;\n            margin-left: -9px;\n        }\n    }\n}\n\n.dash-spreadsheet-menu {\n    display: flex;\n    flex-direction: row;\n\n    & > * {\n        padding-right: 5px;\n    }\n\n    .dash-spreadsheet-menu-item {\n        position: relative;\n\n        .show-hide-menu {\n            background-color: #fafafa;\n            border: 1px solid #d3d3d3;\n            display: flex;\n            flex-direction: column;\n            max-height: 300px;\n            overflow: auto;\n            position: absolute;\n            top: 100%;\n            left: 0;\n            z-index: 500;\n\n            .show-hide-menu-item {\n                display: flex;\n                flex-direction: row;\n                padding: 5px;\n\n                label {\n                    white-space: nowrap;\n                }\n            }\n        }\n    }\n}\n\n.dash-table-container {\n    .previous-next-container {\n        text-align: right;\n        padding: 5px 0px;\n\n        .page-number {\n            font-family: monospace;\n            display: inline-block;\n\n            .last-page {\n                display: inline-block;\n                text-align: center;\n                padding: 1px 2px;\n            }\n\n            .current-page-container {\n                display: inline-block;\n                position: relative;\n\n                .current-page-shadow,\n                input.current-page {\n                    display: inline-block;\n                    border-bottom: solid lightgrey 1px !important;\n                    color: black;\n                    border: none;\n                    text-align: center;\n                    font-family: monospace;\n                    font-size: 10pt;\n                    padding: 1px 2px;\n\n                    &::placeholder {\n                        color: black;\n                    }\n\n                    &:focus {\n                        outline: none;\n\n                        &::placeholder {\n                            opacity: 0;\n                        }\n                    }\n                }\n\n                input.current-page {\n                    position: absolute;\n                    top: 0;\n                    left: 0;\n                    width: 100%;\n                    height: 100%;\n                }\n            }\n        }\n\n        button.previous-page, button.next-page, button.first-page, button.last-page {\n            transition-duration: 400ms;\n            padding: 5px;\n            border: none;\n            display: inline-block;\n            margin-left: 5px;\n            margin-right: 5px;\n\n            &:hover {\n                color: hotpink;\n\n                &:disabled {\n                    color: graytext\n                }\n            }\n\n            &:focus {\n                outline: none;\n            }\n        }\n    }\n\n    .dash-spreadsheet-container {\n        .reset-css();\n        display: flex;\n        flex-direction: row;\n        position: relative;\n\n        // This overrides Bootstrap 3.4.1 body styling\n        // https://github.com/twbs/bootstrap/blob/v3-dev/dist/css/bootstrap.css#L1087\n        // Also unapplies with the latest `in development` 5.0.0-alpha2 (https://github.com/twbs/bootstrap/blob/main/dist/css/bootstrap.css#L51)\n        line-height: initial;\n\n        // This overrides Chrome's default `font-size: medium;` which is causing performance issues\n        // with AutoInputResize sub-component in react-select\n        // https://github.com/JedWatson/react-input-autosize/blob/05b0f86a7f8b16de99c2b31296ff0d3307f15957/src/AutosizeInput.js#L58\n        table {\n            font-size: inherit;\n            pointer-events: none;\n\n            td, th {\n                pointer-events: initial;\n            }\n        }\n\n        input[type=\"radio\"] {\n            // These override Bootstrap 3.4.1 type=\"radio\" styling\n            // https://github.com/twbs/bootstrap/blob/v3-dev/dist/css/bootstrap.css#L2621\n            // This is not a problem with the latest `in development` 5.0.0-alpha2\n            margin: initial;\n            line-height: initial;\n            // These override Bootstrap 4.5.0 type=\"radio\" styling\n            // https://github.com/twbs/bootstrap/blob/v4-dev/dist/css/bootstrap.css#L287\n            // This is not a problem with the latest `in development` 5.0.0-alpha2\n            box-sizing: initial;\n            padding: initial;\n        }\n\n\t    .dash-spreadsheet-inner {\n            box-sizing: border-box;\n            display: flex;\n            flex-direction: column;\n\n            *,\n            *:after,\n            *:before {\n\t\t        box-sizing: inherit;\n            }\n\n            .Select {\n                overflow: hidden;\n                position: static;\n            }\n\n            .Select,\n            .Select-control {\n\t\t        background-color: inherit;\n            }\n\n            .Select-value {\n                display: flex;\n                flex-direction: column;\n                justify-content: center;\n                margin-top: -2px;\n            }\n\n            .marker-row {\n                tr {\n                    visibility: hidden !important;\n                }\n\n                td, th {\n                    height: 0 !important;\n                    padding: 0 !important;\n                    margin: 0 !important;\n                }\n            }\n\n            .dash-filter {\n\t\t        input::placeholder {\n                    color: inherit;\n                    font-size: 0.8em;\n                    padding-right: 5px;\n                }\n\n                & + .dash-filter {\n                    &:not(:hover):not(:focus