            overflow: auto;\n                }\n\n                .cell-0-0,\n                .cell-1-0 {\n                    flex: 0 0 auto;\n                    left: 0;\n                    position: sticky;\n                    position:-webkit-sticky;\n                    z-index: 400;\n                }\n\n                .cell-0-1 {\n                    z-index: 300;\n                    flex: 0 0 auto;\n                }\n\n                .cell-1-1 {\n                    flex: 0 0 auto;\n                }\n            }\n\n            &.dash-fill-width {\n                .cell-0-1,\n                .cell-1-1 {\n                    flex: 1 0 auto;\n                }\n\n                .cell {\n                    table {\n                        width: 100%;\n                    }\n                }\n            }\n\n            td {\n                background-color: inherit;\n\n                &.focused {\n                    margin: -1px;\n                    z-index: 200;\n                }\n\n                .dash-cell-value-container {\n                    width: 100%;\n                    height: 100%;\n                }\n\n                .dash-input-cell-value-container {\n                    position: relative;\n                }\n\n                .dash-cell-value {\n                    height: 100%;\n                    width: 100%;\n\n                    &.unfocused::selection {\n                        background-color: transparent;\n                    }\n\n                    &.unfocused {\n                        caret-color: transparent;\n                    }\n                }\n\n                input.dash-cell-value {\n                    position: absolute;\n                    left: 0;\n                    top: 0;\n                }\n\n                .cell-value-shadow {\n                    margin: auto 0;\n                    opacity: 0;\n                }\n\n                .input-cell-value-shadow {\n                    display: 